.logo {
  height: 50px;
  background: #f0f2f5;
  border: 10px solid #fff;
  border-left: 20px solid #fff;
  border-right: 20px solid #fff;
}
.left-menu .menu-item-link {
  display: inline-block;
}
.left-menu .menu-item-link .collapsed {
  display: none;
}
.navbar {
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
  background: #fff;
  position: absolute;
  bottom: 40px;
  top: 0;
  z-index: 4;
  height: 100%;
}
.navbar .ant-menu {
  border: none!important;
}
.navbar .icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  height: 40px;
  width: 40px;
  font-size: 18px;
  -webkit-box-shadow: 6px 2px 8px rgba(0, 0, 0, 0.05);
          box-shadow: 6px 2px 8px rgba(0, 0, 0, 0.05);
  position: absolute;
  background-color: #fff;
  bottom: 60px;
}
.mask {
  height: 100%;
  position: absolute;
  width: 100%;
  background: #000;
  opacity: 0.3;
  z-index: 3;
}



