@import "font/iconfont.css";
:global(.anticon):before {
  font-family: 'anticon-ts-app', 'anticon' !important;
  display: block!important;
}
.loader {
  -webkit-animation: rotate 1s infinite;
          animation: rotate 1s infinite;
  height: 50px;
  width: 50px;
}
.loader:before,
.loader:after {
  border-radius: 50%;
  content: '';
  display: block;
  height: 20px;
  width: 20px;
}
.loader:before {
  -webkit-animation: ball1 1s infinite;
          animation: ball1 1s infinite;
  background-color: #cb2025;
  -webkit-box-shadow: 30px 0 0 #f8b334;
          box-shadow: 30px 0 0 #f8b334;
  margin-bottom: 10px;
}
.loader:after {
  -webkit-animation: ball2 1s infinite;
          animation: ball2 1s infinite;
  background-color: #00a096;
  -webkit-box-shadow: 30px 0 0 #97bf0d;
          box-shadow: 30px 0 0 #97bf0d;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg) scale(0.8);
    -moz-transform: rotate(0deg) scale(0.8);
  }
  50% {
    -webkit-transform: rotate(360deg) scale(1.2);
    -moz-transform: rotate(360deg) scale(1.2);
  }
  100% {
    -webkit-transform: rotate(720deg) scale(0.8);
    -moz-transform: rotate(720deg) scale(0.8);
  }
}
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg) scale(0.8);
    -moz-transform: rotate(0deg) scale(0.8);
  }
  50% {
    -webkit-transform: rotate(360deg) scale(1.2);
    -moz-transform: rotate(360deg) scale(1.2);
  }
  100% {
    -webkit-transform: rotate(720deg) scale(0.8);
    -moz-transform: rotate(720deg) scale(0.8);
  }
}
@-webkit-keyframes ball1 {
  0% {
    -webkit-box-shadow: 30px 0 0 #f8b334;
            box-shadow: 30px 0 0 #f8b334;
  }
  50% {
    -webkit-box-shadow: 0 0 0 #f8b334;
            box-shadow: 0 0 0 #f8b334;
    margin-bottom: 0;
    -webkit-transform: translate(15px, 15px);
    -moz-transform: translate(15px, 15px);
  }
  100% {
    -webkit-box-shadow: 30px 0 0 #f8b334;
            box-shadow: 30px 0 0 #f8b334;
    margin-bottom: 10px;
  }
}
@keyframes ball1 {
  0% {
    -webkit-box-shadow: 30px 0 0 #f8b334;
            box-shadow: 30px 0 0 #f8b334;
  }
  50% {
    -webkit-box-shadow: 0 0 0 #f8b334;
            box-shadow: 0 0 0 #f8b334;
    margin-bottom: 0;
    -webkit-transform: translate(15px, 15px);
    -moz-transform: translate(15px, 15px);
  }
  100% {
    -webkit-box-shadow: 30px 0 0 #f8b334;
            box-shadow: 30px 0 0 #f8b334;
    margin-bottom: 10px;
  }
}
@-webkit-keyframes ball2 {
  0% {
    -webkit-box-shadow: 30px 0 0 #97bf0d;
            box-shadow: 30px 0 0 #97bf0d;
  }
  50% {
    -webkit-box-shadow: 0 0 0 #97bf0d;
            box-shadow: 0 0 0 #97bf0d;
    margin-top: -20px;
    -webkit-transform: translate(15px, 15px);
    -moz-transform: translate(15px, 15px);
  }
  100% {
    -webkit-box-shadow: 30px 0 0 #97bf0d;
            box-shadow: 30px 0 0 #97bf0d;
    margin-top: 0;
  }
}
@keyframes ball2 {
  0% {
    -webkit-box-shadow: 30px 0 0 #97bf0d;
            box-shadow: 30px 0 0 #97bf0d;
  }
  50% {
    -webkit-box-shadow: 0 0 0 #97bf0d;
            box-shadow: 0 0 0 #97bf0d;
    margin-top: -20px;
    -webkit-transform: translate(15px, 15px);
    -moz-transform: translate(15px, 15px);
  }
  100% {
    -webkit-box-shadow: 30px 0 0 #97bf0d;
            box-shadow: 30px 0 0 #97bf0d;
    margin-top: 0;
  }
}
@-webkit-keyframes scale {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale {
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes showFromNothing {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@keyframes showFromNothing {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadeToShow {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeToShow {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes twinkleVisible {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    opacity: 1;
  }
}
@keyframes twinkleVisible {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  100% {
    opacity: 1;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body,
html,
#root,
.app,
.content-wrapper {
  height: 100%;
}
#root {
  -ms-touch-action: none;
      touch-action: none;
}
.app {
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.ant-menu-submenu-popup .ant-menu-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.ant-menu-submenu-title {
  margin: 0!important;
  height: 3rem!important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.ant-menu-item {
  margin: 0!important;
  height: 3rem!important;
}
.ant-menu-item .menu-item-link {
  line-height: 3rem;
}
#root .ant-menu-inline .ant-menu-item,
#root .ant-menu-inline .ant-menu-submenu-title {
  width: 100%;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  background-color: #fff;
}
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  background-color: #9fc8ee;
}



